.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    width: 100%;
    background-color: #282c34;
    min-height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

ul {
    display: inline-block;
    text-align: left;
}

p.hello {
    color: #282c34;
    font-size: large;
    padding-top: 3px;
    padding-bottom: 3px;
}

col.fillCol {
    padding: 5px;
    display: flex;
    align-items: stretch;
}

div.redBox {
    background-color: red;
}
